class Http {
  /* eslint-disable */
  get(url, params, callback) {
    // GET
    const newUrl = params ? this.build(url, params) : url
    let options = {
      method: "GET"
    }
    // console.log(params,'sss')
    // if (params) options.body = params
    return this.request(newUrl, options, callback)
  }

  post(url, body, callback) {
    // POST
    let options = {
      method: "POST"
    }
    if (body) options.body = body
    return this.request(url, options, callback, 0)
  }

  put(url, body, callback) {
    // PUT
    let options = {
      method: "PUT"
    }
    if (body) options.body = body
    return this.request(url, options, callback, 0)
  }

  delete(url, params, callback) {
    // DELETE
    const newUrl = params ? this.build(url, params) : url
    let options = {
      method: "DELETE"
    }
    // console.log(params,'sss')
    // if (params) options.body = params
    return this.request(newUrl, options, callback)
  }

  downloadFile(url, params, callback) {
    // 
    const newUrl = params ? this.build(url, params) : url
    // let requestData = Object.assign({}, data, {
    //     accessToken: sessionStorage.getItem('accessToken')
    //   });
    axios
      .get(newUrl, {
        responseType: "blob",
        headers: {
          menuUrl: window.location.pathname
        }
      })
      .then(resp => {
        let headers = resp.headers
        let contentType = headers["content-type"]

        // console.log('', headers);
        if (!resp.data) {
          //   console.error('', resp);
          return false
        } else {
          //   console.error('', resp);
          const blob = new Blob([resp.data], { type: contentType })

          const contentDisposition = resp.headers["content-disposition"]
          let fileName = "unknown"
          if (contentDisposition) {
            fileName = window.decodeURI(
              resp.headers["content-disposition"].split("=")[1]
            )
          }
          //   console.log('', fileName);
          //   callback(blob, fileName)
          this.downFile(blob, fileName)
        }
      })
      .catch(function(error) {
        console.log(error)
      })
  }
  downFile(blob, fileName) {
    // IE
    if ("download" in document.createElement("a")) {
      let link = document.createElement("a")
      link.href = window.URL.createObjectURL(blob) // 
      link.download = fileName // 
      link.style.display = "none"
      document.body.appendChild(link)
      link.click() // 
      window.URL.revokeObjectURL(link.href) // blob
      document.body.removeChild(link) // 
    } else {
      // IE10+
      window.navigator.msSaveBlob(blob, fileName)
    }
  }

  // postRes(url, body, callback,resCallBack) { // POST
  //     let options = {
  //         method: 'POST'
  //     }
  //     if (body) options.body = JSON.stringify(body)
  //     return this.request(url, options, callback,1)
  // }
  postAsFormData(url, params, callback) {
    let options = {
      method: "POST"
    }
    if (params) options.body = this.buildFormData(params)
    return this.request(url, options, callback)
  }

  // postForm(url, form, callback) {
  //     let options = {
  //         method: 'POST'
  //     }
  //     if (form) options.body = new FormData(form)
  //     return this.request(url, options, callback)
  // }

  request(url, options, callback, resCallBack) {
    // options.credentials = 'same-origin'
    // options.headers = this.defaultHeader()//headers
    return service({
      method: options.method,
      url: url,
      data: options.body ? options.body : {}
      // withCredentials:true
    })
      .then(function(res) {
        if (res.data.result === 404) {
          // 
          router.replace({
            path: "/login",
            query: { redirect: router.currentRoute.fullPath }
          })
        } else {
          //
          //
          return res
        }

        // if (!res) {
        //     throw ""
        // } else if (res.result == true){
        //     //
        //     if(!resCallBack){
        //         callback(res.data)
        //     }else{
        //         callback(res);
        //     }
        // }else if (res.result == false){
        //     //
        //     console.log('err'+error)
        //     message.warning(res.result_message);

        // } else if (res.errcode == 40001) {

        //     throw "token"
        // } else if (res.errcode == -1) {

        //     return res
        // }
      })
      .catch(function(error) {
        // alert(2333)
        console.log(error, "err")
        return Promise.resolve(error)
      })
  }

  defaultHeader() {
    // 
    const header = {
      Accept: "*/*",
      "Content-Type": "application/json"
    }
    return header
  }

  build(url, params) {
    // URL
    const ps = []
    if (params) {
      for (let p in params) {
        if (p) {
          ps.push(p + "=" + encodeURIComponent(params[p]))
        }
      }
    }
    return url + "?" + ps.join("&")
  }

  buildFormData(params) {
    // 
    function isArrayFn(value) {
      if (typeof Array.isArray === "function") {
        return Array.isArray(value)
      } else {
        return Object.prototype.toString.call(value) === "[object Array]"
      }
    }
    if (params) {
      if (isArrayFn(params)) {
        const data = new FormData()
        // a = [{a: '111'},{b: '2222'}]
        for (let p of params) {
          console.log(p)
          data.append(p.key, p.value)
        }
        return data
      } else {
        const data = new FormData()
        for (let p in params) {
          if (p) {
            data.append(p, params[p])
          }
        }
        return data
      }
    }
  }
}
/* eslint-disable */
export default new Http()
