<template>
  <div id="wangeditor">
    <div ref="editorElem" style="text-align:left"></div>
  </div>
</template>
<script>
import E from "wangeditor";
export default {
  name: "editorElem",
  data() {
    return {
      editor: null,
      editorContent: ""
    };
  },
  props: ["catchData", "content"], // 
  watch: {
    content(val) {
      console.log(val, "content");
      this.editor.txt.html(this.content);
    }
  },
  mounted() {
    const self = this;
    console.log(this.content);
    this.editor = new E(this.$refs.editorElem);
    this.editor.customConfig.onchange = html => {
      this.editorContent = html;
      this.catchData(this.editorContent); // htmlcatchData
    };
    this.editor.customConfig.uploadImgServer = "/operate/shopContentupload.json";
    this.editor.customConfig.withCredentials = true;
    this.editor.customConfig.uploadFileName = "file";
    // 
    this.editor.customConfig.uploadImgMaxSize = 5 * 1024 * 1024;
    this.editor.customConfig.menus = [
      // 
      "head", // 
      "bold", // 
      "fontSize", // 
      "fontName", // 
      "italic", // 
      "underline", // 
      "strikeThrough", // 
      "foreColor", // 
      "backColor", // 
      "link", // 
      "list", // 
      "justify", // 
      "quote", // 
      "emoticon", // 
      "image", // 
      "table", // 
      "code", // 
      "undo", // 
      "redo" // 
    ];
    // 
    this.editor.customConfig.uploadImgHooks = {
      before: function(xhr, editor, files) {
        // 
        // xhr  XMLHttpRequst editor files 
        //  {prevent: true, msg: 'xxxx'} 
        // return {
        //     prevent: true,
        //     msg: ''
        // }

        console.log(files, "111");
        files.forEach(file => {
          const isSize = new Promise(function(resolve, reject) {
            let width = 400;
            let height = 400;
            let _URL = window.URL || window.webkitURL;
            let img = new Image();
            img.onload = function() {
              let valid = img.width == width && img.height == height;
              valid ? resolve() : reject();
            };
            img.src = _URL.createObjectURL(file);
          }).then(
            () => {
              return file;
            },
            () => {
              this.$message.error("400x400!");
              return Promise.reject();
            }
          );
          return isSize;
        });
      },
      success: function(xhr, editor, result) {
        // 
        // xhr  XMLHttpRequst editor result 
        // this.imgUrl = Object.values(result.data).toString()
      },
      fail: function(xhr, editor, result) {
        // 
        // xhr  XMLHttpRequst editor result 
      },
      error: function(xhr, editor) {
        // 
        // xhr  XMLHttpRequst editor 
      },
      timeout: function(xhr, editor) {
        // 
        // xhr  XMLHttpRequst editor 
      },

      //  {errno:0, data: [...]} 
      //  JSON 
      customInsert: function(insertImg, result, editor) {
        // 
        // insertImg editor result 

        //  {url:'....'} 
        if (result.url === "") {
          self.$message.warning("!");
          return false;
        } else {
          let url = result.url; // result.data
          // JSON.stringify(url)    // JSON
          insertImg(url);
        }
        // result  JSON 
      }
    };

    this.editor.create(); // 
    if (!this.content) {
      this.editor.txt.html("");
    } else {
      this.editor.txt.html(this.content);
    }
  }
};
</script>
<style lang="less" scoped>
#wangeditor {
  //   width: 50rem;
}
</style>
<style>
.w-e-text-container {
  z-index: 1800 !important;
}
</style>